	module.exports = {"data":{"site":{"siteMetadata":{"title":"Psyked.co.uk","author":"James Ford"}},"markdownRemark":{"id":"/Users/James/Repositories/portfolio-2018/src/posts/got-satisfaction-getsatisfaction/index.md absPath of file >>> MarkdownRemark","html":"<p>My first introduction to <a href=\"http://getsatisfaction.com/\">GetSatisfaction</a> came through <a href=\"http://www.dehats.com/drupal/?q=node/52\">Flex My Day</a>, and it all looks pretty cool.  The company/service is all about providind a medium for companies with large consumer bases to get in touch with what their clients actually want, which I guess would represent a pretty big investment for a company to figure out and build such a service for themselves, and is probably no small part of GetSatisfaction's success.</p>\n<p>For a company like where I work our clients don't really fall into that category, so I can't see us being interested in the service, but there's some pretty big names on their roster.  The <a href=\"http://developers.getsatisfaction.com/\">developer tools</a> look pretty cool, and coupled with Flex My Day's <a href=\"http://code.google.com/p/flexgetsatisfactionlib/\">Flex class libraries</a>, I imagine it might be a cool base to make some sort of customer feedback AIR tool-thingy.</p>","frontmatter":{"title":"Got satisfaction? GetSatisfaction.","date":"December 10, 2008","tags":["actionscript","flex","apis","customer-feedback","getsatisfaction"],"url":null}}},"pathContext":{"prev":{"frontmatter":{"path":"/blog/grunt-an-introductory-training-deck/","tags":["presentations"],"title":"Grunt: An introductory training deck","image":null,"date":"2013-12-12T12:00:42.000Z"}},"next":{"frontmatter":{"path":"/blog/google-chrome-is-kinda-awesome/","tags":["javascript","chrome","google","webkit"],"title":"Google Chrome is kinda awesome...","image":null,"date":"2008-09-10T13:58:27.000Z"}}}}

